import './Header.scss';
import { ReactComponent as VscodeIcon } from '../../images/vscode_icon.svg';
import { ReactComponent as RestoreIcon } from '../../images/restore_icon.svg';
import { CloseOutlined, LineOutlined } from '@ant-design/icons';
const Header = () => {
	return (
		<>
			<header>
				<div className="start btns">
					<VscodeIcon className="vscode_icon" />
					<div className="reactive">
						<button className="header_btn">(F)</button>
						<button className="header_btn">(E)</button>
						<button className="header_btn"> (S)</button>
						<button className="header_btn">(V)</button>
						<button className="header_btn">(G)</button>
						<button className="header_btn">(R)</button>
						<button className="header_btn">(T)</button>
						<button className="header_btn">(H)</button>
					</div>
				</div>
				<h1>Portfolio - Visual Raccoon Code</h1>
				<div className="end btns">
					<button className="header_btn window_btn">
						<LineOutlined />
					</button>
					<button className="header_btn window_btn">
						<RestoreIcon />
					</button>
					<button className="header_btn window_btn">
						<CloseOutlined />
					</button>
				</div>
			</header>
		</>
	);
};

export default Header;
