import { useMemo } from 'react';
import { useParams } from 'react-router';
import racstagram_v2 from '../../images/racstagram_v2.gif';
import racstagram_v1 from '../../images/racstagram_v1.gif';
import none from '../../images/404.png';
import paint from '../../images/paint_app.gif';
import PdLayout from './PdLayout';
import useMoveTop from '../../hooks/useMoveTop';
import todoList from '../../images/todo_list.png';
import vrcode from '../../images/visual_raccoon_code.gif';

const ProjectDetail = () => {
	useMoveTop();
	const { title } = useParams();
	const { mainTitle, subTitle, img, desc, github, functions, stacks, tistory } =
		useMemo(() => {
			switch (title) {
				case 'Racstagram_V2':
					return {
						mainTitle: 'Racstagram Ver2',
						subTitle: '  Ver2',
						img: racstagram_v2,
						desc: '    1        redux .  , , ,      .',
						github: 'https://github.com/RaccoonCode96/redux_racstagram',
						tistory: 'https://goforit.tistory.com/category/Racstagram_V2',
						functions: [
							[' '],
							[' '],
							[' '],
							[' CRUD'],
							['  '],
							['', 'blue'],
							['', 'blue'],
							[' ', 'blue'],
							['   ( Website, Intro)', 'blue'],
							['input   ', 'blue'],
							['    ', 'blue'],
							['     ', 'blue'],
							[' (Intersection Observer)', 'blue'],
							['Input Validation', 'blue'],
							['   ', 'blue'],
						],
						stacks: [
							'React',
							'Redux-Toolkit',
							'Firebase',
							'Scss',
							'Material UI',
							'Lodash',
						],
					};
				case 'Racstagram_V1':
					return {
						mainTitle: 'Racstagram Ver1',
						subTitle: '  Ver1',
						img: racstagram_v1,
						desc: '        .',
						github: 'https://github.com/RaccoonCode96/nwitter',
						tistory: 'https://goforit.tistory.com/category/Racstagram_V1',
						functions: [
							[' CRUD'],
							[' '],
							[' '],
							[' '],
							['  '],
							['  CSS ', 'blue'],
							['     ', 'blue'],
							[' ', 'blue'],
							[' ', 'blue'],
						],
						stacks: ['React', 'Firebase', 'CSS'],
					};
				case 'Paint-Up':
					return {
						mainTitle: 'Paint-Up',
						subTitle: ' ',
						img: paint,
						desc: '        ,     .',
						tistory: 'https://goforit.tistory.com/category/Paint-Up',
						github: 'https://github.com/RaccoonCode96/paintjs_app',
						functions: [
							['Brush  '],
							['Brush  '],
							[' '],
							['  ', 'blue'],
							['   ', 'blue'],
							['  brush  ', 'blue'],
							['Custom Color Picker', 'blue'],
							['Custom Canvas Sizer', 'blue'],
							[' ', 'blue'],
						],
						stacks: ['HTML', 'CSS', 'JS', 'Canvas'],
					};
				case 'Romentum':
					return {
						mainTitle: 'Romentum',
						subTitle: ', ,  ',
						img: todoList,
						desc: '  Momentum           .  input           .',
						github: 'https://github.com/RaccoonCode96/redux_racstagram',
						tistory: 'https://goforit.tistory.com/category/Romentum',
						functions: [
							['  '],
							[' '],
							[' '],
							['input   ', 'blue'],
							['   ', 'blue'],
						],
						stacks: ['HTML', 'CSS', 'JS'],
					};
				case 'VRCode':
					return {
						mainTitle: 'VRCode',
						subTitle: 'VScode     ',
						img: vrcode,
						desc: 'Visual Studio Code   Scss, React   .      sidebar    nav .        .',
						github: 'https://github.com/RaccoonCode96/redux_racstagram',
						tistory: 'https://goforit.tistory.com/category/Romentum',
						functions: [
							[' '],
							['    ', 'blue'],
							['   ', 'blue'],
							['Project   (Side Bar)', 'blue'],
							['contact ,    ', 'blue'],
						],
						stacks: ['React', 'SCSS', 'AntDesign'],
					};
				default:
					return {
						mainTitle: '  !',
						subTitle: 'url  ',
						img: none,
						desc: '',
						github: '',
						tistory: '',
						functions: [],
						stacks: [],
					};
			}
		}, [title]);
	return (
		<>
			<PdLayout
				title={mainTitle}
				subTitle={subTitle}
				img={img}
				desc={desc}
				github={github}
				functions={functions}
				stacks={stacks}
				tistory={tistory}
			/>
		</>
	);
};

export default ProjectDetail;
